<template>
  <div>
    <div style="display:flex;justify-content:center">
      <div style="margin-right:100px">
        <h4>基本数组操作</h4>
        <div>{{this.arr}}</div><br>
        <input type="text" placeholder="添加的数据" v-model="addData">
        <button @click="startAdd()">数组头部添加</button>
        <button @click="endAdd()">数组末尾添加【数字】</button>
        <button @click="centerAdd()">数组中心项添加</button><span>(若长度为奇数则添加到中心靠前一项)</span>
        <br>
        <button @click="startDel()">数组头部删除</button>
        <button @click="endDel()">数组末尾删除</button>
        <button @click="del()">删除所有不是数字的元素</button>
        <button @click="order()">从大到小排序</button>
        <button @click="deduplication()">数组去重</button>
      </div>
      <div>
        <h4>基本字符串操作</h4>
        <div>{{this.string}}</div><br>
        <input type="text" placeholder="判断的元素" v-model="judmentElement">
        <button @click="elementNum()">判断字符串中某元素的数量</button>
        <br>
        <button @click="stringLength()">统计字符串长度</button>
        <button @click="lowerCase()">将字符串转为全小写</button>
        <button @click="day()">截取日期“2021/6/3”</button>
        <button @click="replaceDay()">将日期中的斜杠替换为“-”</button>
      </div>
    </div>
    <div style="margin-top:50px">
      <button @click="showComponent(true)">显示子组件</button><button @click="showComponent(false)">隐藏子组件</button>
      <componentCube :componentCubeData="arr" v-show="isShow"></componentCube>
    </div>
  </div>
</template>

<script>
import componentCube from './componentCube.vue'
export default {
  name: 'index',
  components:{
		componentCube:componentCube
  },
  data () {
    return {
      arr: [1,2,3,4,5,"abc","d","e"],
      string: "Hello World! 2021/6/3",
      addData: "",
      judmentElement: "",
      isShow: false
    }
  },
  methods:{
    startAdd() {
      this.arr.unshift(this.addData)
    },
    endAdd() {
      if(!!isNaN(Number(this.addData))){
        alert("输入内容不是数字，请重新输入！")
      }else{
        this.arr.push(Number(this.addData))
      }
    },
    centerAdd() {
      let index = parseInt(this.arr.length / 2);
      this.arr.splice(index,0,this.addData)
    },
    startDel() {
      this.arr.shift()
    },
    endDel() {
      this.arr.pop()
    },
    del() {
      let result = [];
      this.arr.map((e)=>{
        if(typeof(e) == "number"){
          result.push(e)
        }
      })
      this.arr = result;
    },
    order(){
      this.arr.sort(function(a,b){
        return a < b ? 1 : -1
      })
    },
    deduplication(){
      this.arr = Array.from(new Set(this.arr))
    },
    elementNum() {
      if(this.judmentElement == ""){
        alert("请在input框中输入元素！")
      }else{
        let num = 0;
        let index = this.string.indexOf(this.judmentElement);
        while(index !== -1){
          num++;
          index = this.string.indexOf(this.judmentElement,index + 1);
        }
        alert("元素" + this.judmentElement + "出现了" + num + "次")
      }
      
    },
    stringLength() {
      alert("字符串长度为:" + this.string.length)
    },
    lowerCase() {
      this.string = this.string.toLowerCase()
    },
    day() {
      this.string = this.string.split(" ")[2]
    },
    replaceDay() {
      this.string = this.string.replace(/\//g,"-")
    },
    showComponent(isShow){
      this.isShow = isShow;
    }
    
  },
  mounted() {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



