<template>
    <div>
        <div style="display:flex;justify-content:center;align-items:center">
            <div style="margin:50px 0px 10px 0px;display:flex;width:330px;height:330px;justify-content:center;flex-wrap:wrap">
                <div v-for="(item,index) in this.showData" :key="index" :class="div[index]">{{item}}</div>
            </div>
        </div>
        <button @click="one()">顺时针旋转一格</button>
        <button @click="beginChange()">开始抽奖</button>
        <button @click="stopChange()">结束抽奖</button>
    </div>
</template>
<script>
export default {
  name: 'componentCube',
  props: ["componentCubeData"],// 从父组件接收的数据
  data () {
    return {
        showData:[],
        div:["div1","div2","div3","div4","div50","div6","div7","div8","div9"],
        timer:null
    }
  },
  methods: {
    init(){
        this.showData = [];
        this.showData.push(...this.componentCubeData)
        if(this.showData[4] != ""){
            this.showData.splice(4, 0, "")
        }
        let len = this.showData.length;
        if(len > 9){
            this.showData.splice(9, len - 9)
        }
        if(len < 9){
            for(let i = 0; i < 9 - len; i++){
                this.showData.push("")
            }
        }
    },
    
    one(){
        this.showData[4] = "";
        this.div[4] = "div50";
        this.showData = this.arrChange(this.showData)
        this.div = this.arrChange(this.div)
    },
    arrChange(arr){
        let [a, b, c, d, e, f, g, h, i] = [...arr];
        [a, b, c, d, e, f, g, h, i] = [d, a, b, g, e, c, h, i, f];
        return [a, b, c, d, e, f, g, h, i]
    },

    beginChange(){
        if(!!this.timer){
            clearInterval(this.timer);
        }
        let self = this;
        self.timer = setInterval(function(){
            self.one();
        },100)
    },
    stopChange(){
        this.showCenter();
        clearInterval(this.timer);
    },
    showCenter(){
        this.showData[4] = "中奖号" + this.showData[1];
        this.div[4] = "div51";
        this.showData = this.showData.slice();
        this.div = this.div.slice();
    }
  },
  mounted() {
      this.init()
  },
  watch: {
    componentCubeData:{ //监听的对象
      deep:true, //深度监听设置为 true
      handler:function(newV,oldV){
        this.init();
      }
    }
  },
}
</script>
<style scoped>
.div1{
    width: 100px;
    height: 100px;
    background: pink;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div2{
    width: 100px;
    height: 100px;
    background: gray;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div3{
    width: 100px;
    height: 100px;
    background: greenyellow;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div4{
    width: 100px;
    height: 100px;
    background: gold;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div50{
    width: 100px;
    height: 100px;
    background: white;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div51{
    width: 100px;
    height: 100px;
    background: red;
    color: yellow;
    font-size: 25px;
    font-weight: bold;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div6{
    width: 100px;
    height: 100px;
    background: blueviolet;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div7{
    width: 100px;
    height: 100px;
    background: orange;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div8{
    width: 100px;
    height: 100px;
    background: wheat;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div9{
    width: 100px;
    height: 100px;
    background: skyblue;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>



